import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            weight: '',
            height: '',
            list1: [],
            list2: [],
            list3: [],
            list4: [],
            patient: this.$store.state.patient,
            info: {
                MaritalStatus: '',
                ReproductiveStatus: '',
                FamilyHealthHistorys:[],
                HealthHistory: '',
                HealthHistoryList:[],
                DrugAllergy: '',
                DrugAllergyList:[],
                SmokingHabit: {
                    SmokingHabit: 0,
                    SmokingNum: 0,
                    SmokingAge: 0
                },
                DrinkingHabit: {
                    DrinkingHabit: 0,
                    DrinkingNum: 0,
                    DrinkingAge: 0
                },
            }
        }
    },
    created(){
        this.pull()
        this.getCategory()
    },
    beforeDestroy(){
        this.$store.dispatch('pushHealthWAndH',{
            weight: this.weight,
            height: this.height,
        })
    },
    beforeRouteEnter(to, from, next) {  //进入前   
        let flag = false
        /*if(from.path == '/payFor' || from.path == '/create-evaluate'){
            flag = true
        }*/ 	
        next(vm => {
            if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || vm.$store.state.isFresh){
	       		flag = true
	       	}
            if(flag){
            	vm.info = {
	                MaritalStatus: '',
	                ReproductiveStatus: '',
	                FamilyHealthHistorys:[],
	                HealthHistory: '',
	                HealthHistoryList:[],
	                DrugAllergy: '',
	                DrugAllergyList:[],
	                SmokingHabit: {
	                    SmokingHabit: 0,
	                    SmokingNum: 0,
	                    SmokingAge: 0
	                },
	                DrinkingHabit: {
	                    DrinkingHabit: 0,
	                    DrinkingNum: 0,
	                    DrinkingAge: 0
	                },
	            }
            	vm.weight = ""
            	vm.height = ""
                vm.pull()
                vm.getCategory()
                vm.$store.dispatch('pushIsFresh',true)
            }
            else{
                setTimeout(()=>{
                    document.documentElement.scrollTop = document.body.scrollTop = to.meta.scollTopPosition;   
                    vm.$store.dispatch('pushIsFresh',true)
                },1)
            }
        });
    },
    methods: {
        pull(){
            this.$get('PlatFormAPI/PatientAccount/GetPatientHealthByPatientId',{
                PatientId: this.patient.PatientId
            }).then(o => {
                if(o){
                    this.$store.dispatch('pushHealth', o)
                    this.height = o.Height
                    this.weight = o.Weight
                    if(o.MaritalStatus){
                        this.info.MaritalStatus = o.MaritalStatus
                    }
                    if(o.ReproductiveStatus){
                        this.info.ReproductiveStatus = o.ReproductiveStatus
                    }
                    if(o.FamilyHealthHistorys){
                        let family = o.FamilyHealthHistorys
                        o.FamilyHealthHistorys.forEach((v,k)=>{
                        	if(v.HealthHistory){
                        		let children = v.HealthHistory.split(";")
                        		children.length = children.length - 1
                        		family[k].children = children
                        	}
                        })
                        this.info.FamilyHealthHistorys = family
                    }
                    if(o.HealthHistory){
                    	let list = o.HealthHistory.split(";")
                    	list.length = list.length - 1
                        this.info.HealthHistory = o.HealthHistory
                        this.info.HealthHistoryList = list
                    }
                    if(o.DrugAllergy){
                        let list = o.DrugAllergy.split(";")
                    	list.length = list.length - 1
                        this.info.DrugAllergy = o.DrugAllergy
                        this.info.DrugAllergyList = list
                    }
                    if(o.SmokingHabit){
                        this.info.SmokingHabit = o.SmokingHabit
                    }
                    if(o.DrinkingHabit){
                        this.info.DrinkingHabit = o.DrinkingHabit
                    }
                    console.log(this.$store.state.HealthWAndH)
                    if(this.$store.state.HealthWAndH.weight){
                        this.weight = this.$store.state.HealthWAndH.weight
                    }
                    if(this.$store.state.HealthWAndH.height){
                        this.height = this.$store.state.HealthWAndH.height
                    }
                }
            })
        },
        getCategory(){
            this.$post('PlatFormAPI/Category/QueryCategoryItem',{
                CategoryIds: [ 2, 47 ]
            }).then(o => {
                const info = this.$store.state.Health
                o.forEach(v => {
                    if(v.CategoryId == 2){
                        this.list1 = v.Items
                    }
                    else if(v.CategoryId == 47){
                        this.list2 = v.Items
                    }
                })
                
            })
        },
    }
}



